body {
  
  color: #04174f;
}
canvas {
  background:#04174f;
  width: '100%';
  padding: 0;
  margin-top: 2rem;
  margin: 0 auto;
  
  /*margin-bottom: 1rem;*/
  display: block;
}
/* The navigation bar */
.navbar-custom {
  overflow: hidden;
  z-index:1;
  background-color: #04174f;
 /* position: fixed; /* Set the navbar to fixed position */
  top: 0; /* Position the navbar at the top of the page */
  width: 100%; /* Full width */
  box-shadow: none !important;
  padding: 0 !important;
}

/* Links inside the navbar */
.navbar a {
  float: left;
  display: block;
  color: #f2f2f2;
  text-align: left;
  padding: 14px 16px;
  text-decoration: none;
}

/* Change background on mouse-over */
.navbar a:hover {
  background: #ddd;
  color: #04174f;
}

/* Main content */
.main {
  margin-top: 100px; /* Add a top margin to avoid content overlay */
}

#about {
  margin-top: 100px; /* Add a top margin to avoid content overlay */
}

#me{
  position: sticky;
  top: 60; 
  z-index: 1;
}

